<template>
  <div>
    <main class="main">
      <div class="d-flex align-items-center flex-column">
        <div class="input-group mb-3 w-50 m-5">
          <input type="text" :class="[empty ? 'error' : '']" v-model="similarWord" class="form-control" placeholder="Búsqueda relacionada" aria-label="Búsqueda relacionada" aria-describedby="button-addon2">
          <button class="btn btn-outline-secondary" type="button" id="button-addon2" v-on:click.enter="getDefaultNews">Buscar noticias</button>
        </div>
        <div class="container d-flex flex-wrap justify-content-between">
          <div class="card m-3 w-25 noticia border-dark rounded" v-for="noticia in defaultNews">
            <a :href="noticia.url"><img :src="noticia.image" class="card-img-top" :alt="noticia.title"></a>
            <div class="card-body">
              <h5 class="card-title">{{noticia.title}}</h5>
              <p class="card-text">{{noticia.description}}</p>
              <p class="card-text"><small class="text-muted">{{ noticia.publishedAt }}</small></p>
            </div>
          </div>
        </div>
      </div>
    </main>
    <footer-component></footer-component>
  </div>
</template>

<script>

import FooterComponent from "../components/FooterComponent";

export default {
  components:{
    FooterComponent
  },
  name: "News",
  data(){
    return{
      defaultNews: [],
      loader: true,
      token:'c61caec5ea5307665bceae731e4cd7cd',
      similarWord: this.$route.params.similarNew,
      empty: false
    }
  },
  created() {
    this.getDefaultNews();

  },
  methods:{
    /**
     * Este método de encarga de buscar en una api de noticias todo el contenido relacionado con la palabara que se intoduce en el buscador
     */
    getDefaultNews(){
      //Si la palabra del buscador esta vacia este metodo no devuelve nada
      if(this.similarWord === ''){
        this.empty = true;
        return
      }
      //En caso de que se introduzca una palabra, se buscará el contenido relacionado en la api y devolvera las noticias relacionadas
      this.empty=false;
      this.axios
          .get(`https://gnews.io/api/v4/search?q=${this.similarWord}&lang=es&token=${this.token}`)
          .then((res) => {
            console.log(res)
            this.defaultNews = res.data.articles;
          })
          .catch((e) => {
            console.log(e.response);
          });
    }
  }


}
</script>

<style scoped>
.main{
  background-color: #ffffff;
}

.noticia{
  box-sizing: border-box;
}
</style>
