<template>
  <div>
    <h2>Crear un informe</h2>
    <form @submit.prevent="crearInforme">
      <div class="mb-4">
        <label class="form-label">Seleccione el paciente</label>
        <select name="hospitalSelect" class="form-control" v-model="usuario" required>
          <option value="" selected>Seleccione un paciente</option>
          <option :value="paciente.nombre" v-for="(paciente, index) in pacientes" :key="index">{{paciente.nombre}}</option>
        </select>
      </div>
      <div class="form-floating textarea">
        <textarea class="form-control" placeholder="motivo" id="floatingText" v-model="informe.descripcion" required></textarea>
        <label for="floatingText">Indique motivo de la consulta, revisión...</label>
      </div>
      <div class="form-floating textarea">
        <textarea class="form-control" placeholder="tratamiento" id="floatingText"  v-model="informe.pruebasRealizadas" required></textarea>
        <label for="floatingText">Indique el tratamiento</label>
      </div>

      <button type="submit" class="btn btn-primary">Crear informe</button>
      <div class="alert alert-primary welcome" role="alert" v-if="creado">
        <h4 class="alert-heading">Informe creado</h4>
        <p>El informe se ha creado correctamente</p>
      </div>
    </form>
  </div>
</template>

<script>
export default {
  name: "InformeDoctor",
  data(){
    return{
      informe:{
        descripcion: '',
        pruebasRealizadas: ''
      },
      pacientes:[],
      usuario:'',
      creado: false
    }
  },
  /**
   * Cuando se crea el componente se cargan los pacientes que tiene el medico asignados para despues poder elegir uno para elaborar el informe
   */
  created() {
    //El nombre del medico se coge de la store a traves de un getter
    const nombre = this.$store.getters.userInfo.nombre;

    this.axios
        .get(`/user/users/${nombre}`)
        .then((res) => {
          this.pacientes = res.data.usuarios;
        })
        .catch((e) => {
          console.log(e.response);
        });
  },
  /**
   * Cuando la variable de data usuario cambia, se guarda en data para poder visualizar el paciente seleccionado
   */
  watch:{
    usuario(newVal,oldVal){
      this.usuario = newVal;
    }
  },
  methods: {
    /**
     * Este metodo se encarga de recoger los datos del formulario y crea el objeto que enviara a la API para que esta pueda guardar el informe en la base de datos
     */
    crearInforme(){
      const doctorId = this.$store.getters.userId;
      const doctor = this.$store.getters.userInfo.nombre;
      const usuario = this.usuario;
      const body = {
        doctorId,
        doctor,
        usuario,
        ...this.informe
      }
      this.axios
          .post(`/informes/nuevo-informe`,body)
          .then((res) => {
            this.creado = true;
          })
          .catch((e) => {
            console.log(e.response);
          });
      setTimeout(()=>{
        this.creado = false;
      },3000)
    }
  }
}
</script>

<style scoped>
form{
  width:75%;
  margin:0 auto;
  border-bottom: 1px solid gray;
}

form+p{
  font-size:180%;
  margin-top:2em;
}

h2{
  padding:1em;
}

button{
  margin:1em;
}

.field label{
  font-size: 110%;
  font-weight:bold;
  margin-right: 0.5em;
}

.textarea{
  margin-top:2em;
}
</style>
