<template>
  <div>
    <main class="main">
      <div class="usermenu">
        <div class="photo"><img src="../assets/iconoUsuario.png" alt="" width="150" /></div>
        <p class="name">{{user.nombre}}</p>
        <ul class="options">
          <li><router-link to="/">Inicio</router-link></li>
          <li><router-link to="/contact">Mi hospital</router-link></li>
          <li><router-link to="/user/profile/date">Crear una cita</router-link></li>
          <li><router-link to="/user/profile/informes">Mis informes</router-link></li>
          <li><router-link to="/user/profile/calendar">Calendario</router-link></li>
        </ul>
      </div>
      <div class="contentMenu">
        <div class="data">
          <div>
            <p class="principal">{{totalCitas}}</p>
            <p>Citas totales</p>
          </div>
          <div>
            <p class="principal">Hospital</p>
            <p>{{user.hospital}}</p>
          </div>
          <div>
            <p class="principal">Medico</p>
            <p>{{user.medico}}</p>
          </div>
        </div>
        <div class="panel">
          <router-view></router-view>
        </div>
      </div>
    </main>
    <footer-component></footer-component>
  </div>
</template>

<script>
import FooterComponent from "../components/FooterComponent";

export default {
  components:{
    FooterComponent
  },
  name: "PanelUser",
  data(){
    return{
      user: {},
      totalCitas:0
    }
  },
  created() {
    //Cuando se crea el componente, se accede a la API REST y se busca el usuario paciente correspondiente para cargar toda la informacion que este contiene
    const userId = this.$store.getters.userId;
    this.axios
        .get(`/user/${userId}`)
        .then((res) => {
          this.user = res.data.usuario;
          this.listarCitas();
        })
        .catch((e) => {
          console.log(e.response);
        });
  },
  methods: {
    /**
     * Este metodo hace una peticion a la APIREST y devuelve todas las citas de un usuario paciente
     */
    listarCitas(){
      const {uid} = this.user;
      this.axios
          .get(`/citas/${uid}`)
          .then((res) => {
            this.totalCitas = res.data.total;
          })
          .catch((e) => {
            console.log(e.response);
          });
    }
  }
}
</script>

<style scoped>
.main{
  display:flex;
  background-color:ghostwhite;
  height:100%;
}

img{
  border-radius: 50%;
}

.usermenu{
  background-color:white;
  margin: 3em 1em 7em 2em;
  width:25%;
  border-radius: 10px;
  box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
  height:100%;
}

.contentMenu{
  background-color:ghostwhite;
  margin: 3em 1em 7em 2em;
  width:75%;
}

.name{
  font-size:130%;
  font-weight:bold;
  margin-bottom:2em;
}

.btnphoto{
  border-radius: 10%;
  background-color: #42b983;
  color:white;
  width:8em;
}
a{
  text-decoration:none;
  display:block;
  padding:1.5em 2em 1em 2em;
  color:gray;
  font-weight: bold;
  border-bottom: 1px solid lightslategray;
}

li:last-child a{
  border-bottom: none;
}

.options{
  list-style: none;
  width:100%;
  margin-top:3em;
  padding:0;
}

.data{
  display:flex;
  align-items: center;
  background-color:white;
  border-radius: 10px;
  box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
  height:7em;
}

.data div{
  border-right: 1px solid lightslategray;
  width:100%;
}

.principal{
  font-weight: bold;
  font-size: 150%;
}

.panel{
  margin-top: 5em;
  background-color:white;
  border-radius: 10px;
  box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
}
</style>
