<template>
  <div>
    <div class="mb-5 d-flex justify-content-center">
      <div class="w-50">
        <h2 class="p-2 m-3 bg-opacity-75 bg-danger text-light rounded-2">CASOS COVID-19</h2>
        <apexchart width="100%" type="bar" :options="options" :series="series"></apexchart>
        <button @click="updateData" class="btn btn-primary w-75">Actualizar datos</button>
      </div>

    </div>
    <footer-component></footer-component>
  </div>
</template>

<script>

import FooterComponent from "../components/FooterComponent";

export default {
  components:{
    FooterComponent
  },
  name: 'Covid',
  data(){
    return{
      options: {
        chart: {
          id: 'Casos COVID-19'
        },
        xaxis: {
          categories: ['Confirmados hoy','Confirmados ayer','Nuevos casos hoy','numero de muertos hoy','numero de muertes ayer'],
          tickPlacement: 'on'
        },
        theme: {
          palette: 'palette7'
        }
      },
      series: [{
        name: 'Casos',
        data: []
      }],
      communities:[],
      casesRegister:[],
      dateActual: ''
    }
  },
  created() {
    this.dataCovid();
  },
  methods:{
    dataCovid(){
      //Se extrae la fecha actual y se busca en la api de datos abiertos de COVID por la fecha del dia de hoy
      //Despues cuando devuelva el json con la informacion se extrae solo lo que se necesita
      this.dateActual = this.formatDateActual();
      this.axios
          .get(`https://api.covid19tracking.narrativa.com/api/${this.dateActual}/country/spain`)
          .then((res) => {
            const {today_confirmed,yesterday_confirmed,today_new_confirmed,today_deaths,yesterday_deaths} = res.data.total;
            console.log(today_confirmed)
            this.series[0].data.push(today_confirmed);
            this.series[0].data.push(yesterday_confirmed);
            this.series[0].data.push(today_new_confirmed);
            this.series[0].data.push(today_deaths);
            this.series[0].data.push(yesterday_deaths);
            this.casesRegister = this.series[0].data;
          })
          .catch((e) => {
            console.log(e);
          });
    },
    formatDateActual(){
      //Aqui se formatea la fecha al formato necesario que requiere la api para buscar por fechas
      const dateMs = Date.now();
      const today = new Date(dateMs);
      const todayModify = today.toLocaleDateString().replace(/\//g,'-');
      const dateArray = todayModify.split('-');
      const day = dateArray[0];
      const month = dateArray[1];
      const year = dateArray[2];
      return year + '-' + day + '-' + month;
    },
    updateData(){
      //Se actualizan los datos de la tabla
      const newData = this.casesRegister;
      this.series = [{
        data: newData
      }]
    }
  }
}

</script>

