<template>
  <div>
  <p>Mis informes</p>
  <table class="table">
    <thead>
    <tr>
      <th scope="col">Doctor</th>
      <th scope="col">Descripcion</th>
      <th scope="col">Prueba</th>
    </tr>
    </thead>
    <tbody>
    <tr v-for="(informe, index) in informes" :key="index">
      <th>{{ informe.doctor }}</th>
      <td>{{ informe.descripcion }}</td>
      <td>{{informe.pruebasRealizadas}}</td>
    </tr>
    </tbody>
  </table>
  </div>
</template>

<script>
export default {
  name: "ListaInformes",
  data(){
    return{
      informes: []
    }
  },
  /**
   * Cuando se crea el componente se cargan los informes que ha realizado el usuario medico haciendo una peticion a la API
   */
  created() {
    const usuario = this.$store.getters.userInfo.nombre;
    this.axios
        .get(`/informes/${usuario}`)
        .then((res) => {
          this.informes = res.data.informes;
        })
        .catch((e) => {
          console.log(e.response);
        });
  }
}
</script>

