<template>
  <div>
    <p>Lista de citas realizadas o pendientes</p>
    <table class="table">
      <thead>
      <tr>
        <th scope="col">Paciente</th>
        <th scope="col">Descripcion</th>
        <th scope="col">Fecha</th>
        <th scope="col">Hora</th>
      </tr>
      </thead>
      <tbody>
      <tr v-for="(cita, index) in citas" :key="index">
        <th>{{ cita.usuario }}</th>
        <td>{{ cita.descripcion }}</td>
        <td>{{cita.fecha}}</td>
        <td>{{cita.hora}}</td>
      </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
export default {
  name: "ListaCitasPacientes",
  data(){
    return{
      citas: []
    }
  },
  /**
   * Cuando se crea el componente se cargan las citas pendientes o realizadas del usuario medico
   */
  created() {
    const usuario = this.$store.getters.userInfo.nombre;
    this.axios
        .get(`/citas/history/${usuario}`)
        .then((res) => {
          this.citas = res.data.citasDB;
        })
        .catch((e) => {
          console.log(e.response);
        });
  }
}
</script>

