<template>
  <div class="container w-75 bg-primary mt-5 rounded shadow mb-5">
    <div class="row align-items-stretch">
      <div class="col ground d-none d-lg-block col-md-5 col-lg-5 col-xl-6 rounded">

      </div>
      <div class="col bg-white p-5 rounded-end">
        <div class="text-end">
          <img src="../assets/LogoVallasalud.png" width="100%" height="100%" alt="Imagen del logo de la página"/>
        </div>
        <h2 class="fw-bold text-center py-5">Iniciar sesión</h2>
        <form @submit.prevent="login">
          <div class="mb-4">
            <label for="email" class="form-label">Correo electrónico</label>
            <input type="email" class="form-control" name="email" v-model="usuario.email" id="email" required/>
          </div>
          <div class="mb-4">
            <label for="password" class="form-label">Contraseña</label>
            <input type="password" class="form-control" name="password" v-model="usuario.password" id="password" required/>
          </div>
          <div class="alert alert-danger" role="alert" v-if="mensaje !== ''">
            {{ mensaje }}
          </div>
          <div class="mb-4 form-check">
            <input type="checkbox" class="form-check-input" name="connected" id="connected" v-model="usuario.isDoctor"/>
            <label for="connected" class="form-check-label">Marque la casilla si pertenece al grupo médico</label>
          </div>
          <div class="d-grid">
            <button type="submit" class="btn btn-primary">Iniciar Sesión</button>
          </div>

          <div class="my-3">
            <span>¿No tienes cuenta? <router-link to="/register">Regístrate</router-link></span>
          </div>
        </form>

        <div class="container w-100 my-5">
          <div class="row text-center">
            <div class="col-12">Iniciar Sesión</div>
          </div>
          <div class="row">
            <div class="col">
              <button class="btn btn-outline-primary w-100 my-1">
                <div class="row align-items-center">
                  <div class="col-2 d-none d-md-block">
                    <img src="../assets/facebook.png" alt="Logo de Facebook" width="32">
                  </div>
                  <div class="col-12 col-md-10 text-center">Facebook</div>
                </div>
              </button>
            </div>
            <div class="col">
              <button class="btn btn-outline-danger w-100 my-1">
                <div class="row align-items-center">
                  <div class="col-2 d-none d-md-block">
                    <img src="../assets/Google-Plus-Logo.png" alt="Logo de Facebook" width="32">
                  </div>
                  <div class="col-12 col-md-10 text-center">Google</div>
                </div>
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

import {mapActions} from 'vuex';


export default {
  name: "FormLogin",
  data() {
    return {
      usuario: {
        email: '',
        password: '',
        isDoctor: false
      },
      mensaje:''
    }
  },
  methods: {
    //Importacion de los actions de la Store
    ...mapActions(['guardarUsuario','infoUser','checkDoctor']),
    /**
     * Metodo login que se encarga de iniciar la sesion de un usuario
     * Hace una llamada a la API y con los datos obtenidos los guarda dentro de la Store, además comprueba si el usuario es medico o paciente para redirigirle a su perfil
     */
    login(){
      this.axios.post('/auth/login', this.usuario)
          .then( res => {
            this.infoUser(res.data.usuario);
            this.checkDoctor(res.data.isDoctor);
            this.guardarUsuario(res.data.token);
            if(res.data.isDoctor){
              this.$router.push({name:'doctorProfile'})
            }else{
              this.$router.push({name:'userProfile'})
            }
          })
          .catch( e => {
            this.mensaje = e.response.data.errors || e.response.data.msg
          })
    }
  }
};
</script>

<style scoped>
.ground{
  background-image: url('../assets/imgForm.jpg');
  background-position: center center;
  background-size: cover;
}
</style>
