<template>
<div>
  <h2>Crear una cita</h2>
    <form @submit.prevent="crearCita">
      <div class="form-floating textarea">
        <textarea class="form-control" placeholder="Leave a comment here" id="floatingText" v-model="fields.descripcion" required></textarea>
        <label for="floatingText">Indique el motivo de la consulta</label>
      </div>
      <div class="field">
        <label>Introduzca la hora</label>
        <input type="time" v-model="fields.hora" required>
        <label for="fecha">Introduzca la fecha</label>
        <input type="date" name="fecha" id="fecha" v-model="fields.fecha" required>
      </div>
      <button type="submit" class="btn btn-primary">Crear cita</button>
    </form>

  <p>Mis citas</p>
  <table class="table">
    <thead>
    <tr>
      <th scope="col">Usuario</th>
      <th scope="col">Descripcion</th>
      <th scope="col">Doctor</th>
      <th scope="col">Hospital</th>
      <th scope="col">Fecha</th>
      <th scope="col">Hora</th>
    </tr>
    </thead>
    <tbody>
    <tr v-for="(cita, index) in citas" :key="index">
      <th>{{ cita.usuario }}</th>
      <td>{{ cita.descripcion }}</td>
      <td>{{cita.doctor}}</td>
      <th>{{ cita.hospital }}</th>
      <td>{{ cita.fecha }}</td>
      <td>{{cita.hora}}</td>
    </tr>
    </tbody>
  </table>
</div>
</template>

<script>
export default {
  name: "DateDoctor",
  data(){
    return{
      fields:{
        descripcion:'',
        hora:'',
        fecha:''
      },
      user:{},
      citas: [],
    }
  },
  /**
   * Metodo que se ejecuta al crearse el componente
   * Hace una llamada a la API REST y obtiene la informacion del medico actual
   */
  created() {
    const isDoctor = this.$store.getters.isDoctor;
    let param = 'no';
    const userId = this.$store.getters.userId;
    if (isDoctor){
      param = 'yes'
    }
    this.axios
        .get(`/user/${userId}?${param}`)
        .then((res) => {
          this.user = res.data.usuario;
          this.listarCitas();
        })
        .catch((e) => {
          console.log(e.response);
        });
  },
  methods: {
    /**
     * Metodo que crea una cita a partir de los datos que se recogen del formulario
     * Recoge los datos del formulario y despues hace la peticion a la api para que esta guarde la informacion en la base de datos
     */
    crearCita(){
      const {nombre, uid, medico,hospital} = this.user;
      let body = {
        ...this.fields,
        usuarioId:uid,
        usuario:nombre,
        doctor:medico,
        hospital
      }
      this.axios
          .post(`/citas/nueva-cita`,body)
          .then((res) => {
            this.citas.push(res.data)
          })
          .catch((e) => {
            console.log(e.response);
          });
    },
    /**
     * Metodo que lista todas las citas que tiene un paciente
     * Hace una llamada a la API REST y obtiene la informacion de todas las citas en formato json
     */
    listarCitas(){
      const {uid} = this.user;
      this.axios
          .get(`/citas/${uid}`)
          .then((res) => {
            this.citas = res.data.citas;
          })
          .catch((e) => {
            console.log(e.response);
          });
    }
  }
}
</script>

<style scoped>
form{
  width:75%;
  margin:0 auto;
  border-bottom: 1px solid gray;
}

form+p{
  font-size:180%;
  margin-top:2em;
}

h2{
  padding:1em;
}

button{
  margin:1em;
}

.field label{
  font-size: 110%;
  font-weight:bold;
  margin-right: 0.5em;
}


.field{
  margin-top:2em;
  padding:2em;
}

.textarea{
  margin-top:2em;
}
</style>
