<template>
  <div class="container w-75 bg-primary mt-5 rounded shadow mb-5">
    <div class="row align-items-stretch" v-if="!congratulations">
      <div class="col ground d-none d-lg-block col-md-5 col-lg-5 col-xl-6 rounded">
      </div>
      <div class="col bg-white p-5 rounded-end">
        <div class="text-end">
          <img src="../assets/LogoVallasalud.png" width="100%" height="100%" alt="Imagen del logo de la página"/>
        </div>
        <h2 class="fw-bold text-center py-5">Registrarse</h2>
        <form @submit.prevent="register">
          <div class="mb-4">
            <label for="tarjeta" class="form-label">Dirección de correo electrónico</label>
            <input type="email" class="form-control" name="email"  id="tarjeta" required v-model="user.email"/>
          </div>
          <div class="mb-4">
            <label for="password" class="form-label">Contraseña</label>
            <input type="password" class="form-control" name="password"  id="password" required v-model="user.password"/>
          </div>
          <div class="mb-4">
            <label for="nombre" class="form-label">Nombre completo</label>
            <input type="text" class="form-control" name="nombre"  id="nombre" required v-model="user.nombre"/>
          </div>
          <div class="mb-4">
            <label for="direction" class="form-label">Dirección</label>
            <input type="text" class="form-control" name="password"  id="direction" required v-model="user.direccion"/>
          </div>
          <div class="mb-4">
            <label class="form-label">Seleccione su hospital</label>
            <select name="hospitalSelect" class="form-control" v-model="hospital">
              <option value="" selected>Seleccione un hospital</option>
              <option value="Hospital Clinico">Hospital Clinico Valladolid</option>
              <option value="Rio Hortega" selected>Hospital Rio Hortega</option>
              <option value="Hospital Rondilla">Hospital Rondilla</option>
            </select>
          </div>
          <div class="mb-4" v-if="hospital !== '' ">
            <label class="form-label">Seleccione su médico</label>
            <select class="form-control" v-model="user.medico">
              <option value="" selected>Seleccione el médico que le pertenece</option>
              <option v-for="doctor in doctors" :value="doctor.nombre">{{doctor.nombre}}</option>
            </select>
          </div>
          <div class="d-grid">
            <button type="submit" class="btn btn-primary">Registrarse</button>
          </div>

          <div class="my-3">
            <span>¿Ya tienes cuenta? <router-link to="/login">Inicia sesion</router-link></span>
          </div>
        </form>

        <div class="container w-100 my-5">
          <div class="row text-center">
            <div class="col-12">Iniciar Sesión</div>
          </div>
          <div class="row">
            <div class="col">
              <button class="btn btn-outline-primary w-100 my-1">
                <div class="row align-items-center">
                  <div class="col-2 d-none d-md-block">
                    <img src="../assets/facebook.png" alt="Logo de Facebook" width="32">
                  </div>
                  <div class="col-12 col-md-10 text-center">Facebook</div>
                </div>
              </button>
            </div>
            <div class="col">
              <button class="btn btn-outline-danger w-100 my-1">
                <div class="row align-items-center">
                  <div class="col-2 d-none d-md-block">
                    <img src="../assets/Google-Plus-Logo.png" alt="Logo de Facebook" width="32">
                  </div>
                  <div class="col-12 col-md-10 text-center">Google</div>
                </div>
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="alert alert-primary welcome" role="alert" v-if="congratulations">
      <h4 class="alert-heading">¡BIENVENIDO!</h4>
      <p>Gracias por registrate en Vallasalud, ahora podrá gestionar todos los temas médicos de manera más sencilla</p>
      <hr>
      <p class="mb-0">Está siendo redirigido a la página de inicio de sesión.</p>
    </div>
  </div>
</template>

<script>


export default {
  name: "FormRegister",
  data(){
    return{
      user:{
        nombre: '',
        email: '',
        password: '',
        medico:'',
        direccion: '',
      },
      doctors: [],
      hospital: '',
      congratulations: false,
      mensaje:''
    }
  },
  methods: {
    /**
     * El metodo register se encarga de recoger los datos del formulario y hacer una peticion a la API para que mande los datos y los registre en la base de datos
     * Si todo ha funcionado correctamente el usuario será redirigido a login para que inicie sesion
     */
    register(){
      this.user.hospital = this.hospital;
      this.axios.post('/user', this.user)
          .then( res => {
            this.congratulations = true;
            setTimeout(()=>{
              this.$router.push({name:'login'})
            },5000)
          })
          .catch( e => {
            this.mensaje = e.response.data.errors || e.response.data.msg
          })
    }
  },
  watch:{
    /**
     * Esta funciones es un watcher, que se ejecuta cuando una propiedad de data cambia, en este caso la propiedad hospital
     * Cuando se selecciona un hospital en el select del formulario se hace una peticion a la API y devuelve todos los medicos de ese hospital
     * cuando el select cambia porque se escoge otra opcion vuelve a realizar otra busqueda
     * @param newVal
     * @param oldVal
     */
    hospital(newVal,oldVal){
      this.axios
          .get(`/hospital/${newVal}/doctors`)
          .then((res) => {
            this.doctors = res.data.DoctorsBD;
          })
          .catch((e) => {
            console.log(e.response);
          });
    }
  }
}
</script>

<style scoped>
.welcome{
margin-bottom:100px;
}

.ground{
  background-image: url('../assets/imgForm.jpg');
  background-position: center center;
  background-size: cover;
}
</style>
