<template>
    <div>
        <p>PACIENTES</p>
        <table class="table">
            <thead>
            <tr>
                <th scope="col">Nombre</th>
                <th scope="col">Hospital</th>
                <th scope="col">Direccion</th>
              <th scope="col">Email</th>
            </tr>
            </thead>
            <tbody>
            <tr v-for="(paciente, index) in pacientes" :key="index">
            <th>{{ paciente.nombre }}</th>
            <td>{{ paciente.hospital }}</td>
            <td>{{ paciente.direccion }}</td>
            <td>{{ paciente.email }}</td>
            </tr>
        </tbody>
    </table>
</div>
</template>

<script>
export default {
    name: "ListaPacientes",
    data(){
    return{
    pacientes: []
      }
    },
  /**
   * Cuando se crea el componente se cargan los pacientes que tiene asignados un medico, esto se hace a traves de una peticion a la API REST
   */
    created() {
    const usuario = this.$store.getters.userInfo.nombre;
    this.axios
    .get(`/hospital/${usuario}`)
    .then((res) => {
    this.pacientes = res.data.patientsDB;
})
    .catch((e) => {
    console.log(e.response);
});
}
}
</script>

<style scoped>
p{
  font-size: 150%;
  font-weight: bold;
  padding:1em;
}
</style>
